* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@keyframes fire {
  50% {
    transform: translate(-50%, 0) scale(0.9, 1);
  }
  100% {
    transform: translate(-50%, 0) scale(1, 0.9);
  }
}
@keyframes travel1 {
  0% {
    left: 150px;
    top: 100px;
  }
  50% {
    transform: rotate(60deg);
  }
  100% {
    left: calc(100% - 350px);
    top: 150px;
  }
}
@keyframes travel2 {
  0% {
    left: calc(100% - 350px);
    top: 150px;
  }
  50% {
    transform: rotate(-160deg);
  }
  100% {
    left: calc(100% - 460px);
    top: calc(100% - 350px);
  }
}
@keyframes travel3 {
  0% {
    left: calc(100% - 460px);
    top: calc(100% - 350px);
  }
  50% {
    transform: rotate(-60deg);
  }
  100% {
    left: 200px;
    top: calc(100% - 300px);
  }
}
@keyframes travel4 {
  0% {
    left: 200px;
    top: calc(100% - 300px);
  }
  50% {
    transform: rotate(10deg);
  }
  100% {
    left: 150px;
    top: 100px;
  }
}
.container {
  width: 100vw;
  height: 100vh;
  background: rgb(18, 43, 71);
  background: url(../images/stars.png),
  linear-gradient(180deg, rgba(18, 43, 71, 1) 0%, rgba(18, 20, 37, 1) 51%, rgba(46, 25, 54, 1) 100%);
  position: relative;
  background-size: 200px 200px, cover;
  min-width: 1200px;
  min-height: 800px;
}
.rocket {
  animation-name: travel1, travel2, travel3, travel4;
  animation-duration: 2s;
  animation-delay: 0s, 2s, 4s, 6s;
  width: 89px;
  height: 150px;
  position: absolute;
  left: 150px;
  top: 100px;
  z-index: 2;
  span {
    display: block;
    background: url(../images/rocket.png);
    width: 89px;
    height: 150px;
    position: relative;
    z-index: 1;
  }
  &.fly {
    &::after {
      content: '';
      background: url(../images/fire.png);
      background-size: cover;
      width: 42px;
      height: 67px;
      position: absolute;
      left: 50%;
      top: 87%;
      transform: translate(-50%, 0);
      animation: fire 0.1s 0s infinite alternate;
      transform-origin: 50% 0;
      z-index: 0;
    }
  }
}
.planet1 {
  position: absolute;
  background: url(../images/planet1.png);
  width: 228px;
  height: 229px;
  left: 150px;
  top: 100px;
  border-radius: 50%;
  box-shadow: 0 0 30px rgba(176, 22, 4, 0.4), 0 0 100px rgba(249, 149, 34, 0.2);
}
.planet2 {
  position: absolute;
  background: url(../images/planet2.png);
  width: 201px;
  height: 201px;
  left: calc(100% - 350px);
  top: 150px;
}
.planet3 {
  position: absolute;
  background: url(../images/planet3.png);
  width: 257px;
  height: 246px;
  left: calc(100% - 460px);
  top: calc(100% - 350px);
}
.planet4 {
  position: absolute;
  background: url(../images/planet4.png);
  width: 225px;
  height: 225px;
  left: 200px;
  top: calc(100% - 300px);
  border-radius: 50%;
  box-shadow: 0 0 50px rgba(247, 215, 76, 0.2);
}